.section-form-buttons button:disabled {
  background-color: #ccc; /* Gray color to indicate disabled state */
  color: #666; /* Darker gray for text */
  cursor: not-allowed; /* Change cursor to indicate non-interactivity */
  opacity: 0.6;
}

.submit-button .tail-spin-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.submit-button {
  display: flex;
  align-items: center;
  justify-content: center;
}
