* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif, 'Poppins', sans-serif;
  /* overflow-y: hidden;  */
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #0f1115;
}

.about {
  padding: 15px 0 20px 0;
  text-align: center;
  overflow-y: hidden;
}

.about h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: white;
}

.about p {
  font-size: 1rem;
  color: wheat;
  max-width: 800px;
  margin: 0 auto;
}

.about-info {
  margin: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: left;
  overflow-x: hidden;
}

.about-img {
  width: 35rem;
  height: 35rem;
  overflow-x: hidden;
}

.about-img img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  object-fit: contain;
}

.about-text {
  margin-top: -2rem;
  font-family: 'Poppins', sans-serif;
}

.about-info p {
  font-size: 1.3rem;
  margin: 0 2rem;
  text-align: justify;
}

@media (max-width: 768px) {
  .about h1 {
    font-size: 2rem;
  }

  .about p {
    font-size: 0.9rem;
  }

  .about-info {
    flex-direction: column;
    text-align: center;
  }

  .about-img {
    width: 60%;
    height: 60%;
    margin-bottom: 1rem;
  }

  .about-info p {
    margin: 1rem 2rem;
  }

  .about button {
    margin: 1rem 2rem;
    width: 10rem;
  }
}
